/**
 * The debounced function is called only if no other call are made
 * before the delay is expired. 
 * Any additional calls before the end will prolong the delay.
 * The delay is timed after the first call, any additional call have no effect on it.
 * @param {Function} callback The function to call after waiting
 * @param {Number} delay The delay to wait
 */

function Debouncer(callback, wait) {
    let timeout = null;
    return (...args) => {
        const next = function debouncedFunction() {
            callback(...args)
        };
        clearTimeout(timeout);
        timeout = setTimeout(next, wait);
    }
}

export default Debouncer;
